import { state, State } from '../app/state'
import Reducer from '../app/reducer'
import * as action from '../app/action'
import Page from '../components/page';

export default class Tool {

    public static pageTrans(pageId: number) {
        Page.allPages.get(pageId).outerWrapper.style.opacity = '0';
        Page.allPages.get(pageId + 1).outerWrapper.style.opacity = '1';
        setTimeout(() => {
            Page.allPages.get(pageId).outerWrapper.style.display = 'none';
        }, 700);
    }

    public static resizeWidgets(svgContainerId: string = ''): void {
        // this.resizeSvgContainer(svgContainerId);
        this.resizePlayerContainer();
    }

    public static resizePlayerContainer(): void {
        //resize player
        // player.resizePlayer(player.widget.clientWidth - 160);
    }
}