import rTrain1_1 from '../assets/img/readingTrain/reading1_1.gif'
import rTrain1_2 from '../assets/img/readingTrain/reading1_2.gif'
import rTrain1_3 from '../assets/img/readingTrain/reading1_3.gif'
import rTrain1_4 from '../assets/img/readingTrain/reading1_4.gif'
import rTrain2_1 from '../assets/img/readingTrain/reading2_1.gif'
import rTrain2_2 from '../assets/img/readingTrain/reading2_2.gif'
import rTrain2_3 from '../assets/img/readingTrain/reading2_3.gif'
import rTrain2_4 from '../assets/img/readingTrain/reading2_4.gif'
import rTest1_1 from '../assets/img/readingTest/reading1_1.gif'
import rTest1_2 from '../assets/img/readingTest/reading1_2_r.gif'
import rTest1_3 from '../assets/img/readingTest/reading1_3.gif'
import rTest1_4 from '../assets/img/readingTest/reading1_4.gif'
import rTest2_1 from '../assets/img/readingTest/reading2_1.gif'
import rTest2_2 from '../assets/img/readingTest/reading2_2.gif'
import rTest2_3 from '../assets/img/readingTest/reading2_3_r.gif'
import rTest2_4 from '../assets/img/readingTest/reading2_4.gif'
import rTest3_1 from '../assets/img/readingTest/reading3_1_r.gif'
import rTest3_2 from '../assets/img/readingTest/reading3_2.gif'
import rTest3_3 from '../assets/img/readingTest/reading3_3.gif'
import rTest3_4 from '../assets/img/readingTest/reading3_4.gif'

import '../assets/style/page.scss'
import Reducer from '../app/reducer'
import * as action from '../app/action'
import { state } from '../app/state'
import Tool from '../util/tool';
import { recorder } from '../app/recorder';

export default class Page {
    static PageCounter: number = 0;
    static USER_NAME_INPUT_ID: string = 'userName';
    static RESEARCH_INPUT_ID: string = 'researchArea';
    static USED_TOOL_INPUT_ID: string = 'usedTool';
    static FINISH_BTN_CONTENT: string = 'All Done';
    static allPages: Map<number, Page> = new Map();

    public id: number;
    public hasOption: boolean = false;
    public isTraining: boolean = false;
    public parentContainer: HTMLDivElement;
    public outerWrapper: HTMLDivElement;
    public container: HTMLDivElement;
    public nextBtn: HTMLDivElement;

    constructor(parentContainer: HTMLDivElement) {
        this.createPage(parentContainer);
    }

    public createPage(parentContainer: HTMLDivElement) {
        this.id = Page.PageCounter;
        Page.allPages.set(this.id, this);
        Page.PageCounter++;
        Reducer.triger(action.UPDATE_Z_INDEX, null);
        this.parentContainer = parentContainer;
        this.outerWrapper = document.createElement('div');
        this.outerWrapper.className = 'page';
        this.outerWrapper.style.zIndex = `${state.zIdx}`;
        this.container = document.createElement('div');
        this.container.className = 'inner-wrapper';
        this.outerWrapper.appendChild(this.container);
        this.parentContainer.appendChild(this.outerWrapper);
    }

    public createTitle(content: string) {
        const title: HTMLHeadingElement = document.createElement('h1');
        title.innerHTML = content;
        this.container.appendChild(title);
    }

    public createSubTitle(content: string) {
        const title: HTMLHeadingElement = document.createElement('h2');
        title.innerHTML = content;
        this.container.appendChild(title);
    }

    public createForm() {
        const formWrapper: HTMLDivElement = document.createElement('div');
        formWrapper.className = 'form-wrapper';
        formWrapper.appendChild(this.createSpan('name'));
        formWrapper.appendChild(this.createInput(Page.USER_NAME_INPUT_ID));
        formWrapper.appendChild(this.createSpan('research area'));
        formWrapper.appendChild(this.createInput(Page.RESEARCH_INPUT_ID));
        formWrapper.appendChild(this.createSpan('video editing experience (ae, Adobe Premiere, ...)'));
        const usedToolExp: HTMLInputElement = this.createInput(Page.USED_TOOL_INPUT_ID);
        usedToolExp.defaultValue = 'none';
        formWrapper.appendChild(usedToolExp);
        const confirmBtn: HTMLDivElement = document.createElement('div');
        confirmBtn.innerHTML = 'Getting Started';
        confirmBtn.className = 'btn';
        confirmBtn.onclick = () => {
            //check form
            let flag: boolean = true;
            if ((<HTMLInputElement>document.getElementById(Page.USER_NAME_INPUT_ID)).value === '') {
                alert('Please enter your name');
                flag = false;
            }
            if ((<HTMLInputElement>document.getElementById(Page.RESEARCH_INPUT_ID)).value === '') {
                alert('Please enter your research area');
                flag = false;
            }

            if (flag) {
                recorder.recordUserInfo({
                    name: (<HTMLInputElement>document.getElementById(Page.USER_NAME_INPUT_ID)).value,
                    researchArea: (<HTMLInputElement>document.getElementById(Page.RESEARCH_INPUT_ID)).value,
                    vToolExp: (<HTMLInputElement>document.getElementById(Page.USED_TOOL_INPUT_ID)).value
                })
                Tool.pageTrans(this.id);
            }
        }
        formWrapper.appendChild(confirmBtn);
        this.container.appendChild(formWrapper);
    }

    public createSpan(str: string): HTMLSpanElement {
        const span: HTMLSpanElement = document.createElement('span');
        span.innerHTML = str;
        return span;
    }

    public createInput(id: string): HTMLInputElement {
        const input: HTMLInputElement = document.createElement('input');
        input.id = id;
        return input;
    }

    public createSplitLine(top: number) {
        const hr: HTMLHRElement = document.createElement('hr');
        hr.style.marginTop = `${top}px`;
        this.container.appendChild(hr);
    }

    public createVideo(src: string, top: number, left: number, width: number) {
        const video: HTMLVideoElement = document.createElement('video');
        video.controls = true;
        video.style.marginTop = `${top}px`;
        video.style.marginLeft = `${left}px`;
        video.style.width = `${width}px`;
        const source: HTMLSourceElement = document.createElement('source');
        source.src = src;
        source.type = 'video/mp4';
        video.appendChild(source);
        this.container.appendChild(video);
    }

    public createDescription(content: string, fontSize: number, top: number, left: number, width: number) {
        const span: HTMLSpanElement = document.createElement('span');
        span.className = 'description';
        span.innerHTML = content;
        span.style.fontSize = `${fontSize}pt`;
        span.style.marginTop = `${top}px`;
        span.style.marginLeft = `${left}px`;
        span.style.width = `${width}px`;
        this.container.appendChild(span);
    }

    public createImage(src: string, top: number, left: number, width: number) {
        const img: HTMLImageElement = document.createElement('img');
        img.src = src;
        img.style.marginTop = `${top}px`;
        img.style.marginLeft = `${left}px`;
        img.style.width = `${width}px`;
        this.container.appendChild(img);
    }

    public createImgContainer(src: string, top: number, left: number, height?: number) {
        const imgContainer: HTMLDivElement = document.createElement('div');
        imgContainer.className = 'img-container';
        imgContainer.style.marginTop = `${top}px`;
        imgContainer.style.marginLeft = `${left}px`;
        if (height) {
            imgContainer.style.height = `${height}px`;
        }
        const img: HTMLImageElement = document.createElement('img');
        img.src = src;
        imgContainer.appendChild(img);
        this.container.appendChild(imgContainer);
    }

    public createMiddleText(content: string) {
        const contentContiner: HTMLParagraphElement = document.createElement('p');
        contentContiner.innerHTML = content;
        this.container.appendChild(contentContiner);
    }

    public createVisLangMenu(highlight: number) {
        this.container.innerHTML += `
            <h2>Viusal Language</h2>
            <ol>
                <li class=${highlight === 0 ? 'highlight-list-item' : ''}>Tutorial
                    <ul>
                        <li>Introduce the visual language used to represent animated visualization charts.</li>
                    </ul>
                </li>
                <li class=${highlight === 1 ? 'highlight-list-item' : ''}>Training
                    <ul>
                        <li>You will see 2 examples, each example contains one chart animation represented with visual language and a series of animations. You need to choose which animation corresponds to the one represented with the visual language. The right answer will be shown to you after you made your choice. </li>
                    </ul>
                </li>
                <li class=${highlight === 2 ? 'highlight-list-item' : ''}>Test
                    <ul>
                        <li>Do 3 more Read&Choose test.</li>
                    </ul>
                </li>
            </ol>
        `
    }

    public createRepMenu(highlight: number) {
        this.container.innerHTML += `
            <h2>Animation Reproduction with CAST</h2>
            <ol>
                <li class=${highlight === 0 ? 'highlight-list-item' : ''}>Tutorial
                    <ul>
                        <li>Introduce all interactions in CAST.</li>
                    </ul>
                </li>
                <li class=${highlight === 1 ? 'highlight-list-item' : ''}>Training
                    <ul>
                        <li>You will be asked to reproduce 4 example animations with CAST. And you can ask questions or check your reproduction result in this phase. </li>
                    </ul>
                </li>
                <li class=${highlight === 2 ? 'highlight-list-item' : ''}>Test
                    <ul>
                        <li>4 more reproduction result.</li>
                    </ul>
                </li>
            </ol>
        `
    }


    public createMenu(beforeTxt: string, listItems: string[], highlights: number[], top: number, left: number, width: number) {
        const menuContainer: HTMLDivElement = document.createElement('div');
        menuContainer.className = 'menu-container';
        menuContainer.style.marginTop = `${top}px`;
        menuContainer.style.marginLeft = `${left}px`;
        menuContainer.style.width = `${width}px`;
        let innerItems = '';
        listItems.forEach((item: string, idx: number) => {
            innerItems += `<li class=${highlights.includes(idx) ? 'highlight-list-item' : ''}>${item}</li>`
        })
        menuContainer.innerHTML = `
            <p>${beforeTxt}</p>
            <ul>
                ${innerItems}
            </ul>
            `
        this.container.appendChild(menuContainer);
    }

    public createLaunchBtn(name: string) {
        const jumpBtn: HTMLDivElement = document.createElement('div');
        jumpBtn.innerHTML = 'Load This Chart in CAST';
        jumpBtn.className = 'launch-btn';
        jumpBtn.onclick = () => {
            window.open(`https://tgeconf.github.io/publicUserStudy/system/?chartName=${name}`);
        }
        this.container.appendChild(jumpBtn);
    }

    public createNextBtn(content: string, top?: number, left?: number) {
        this.nextBtn = document.createElement('div');
        this.nextBtn.innerHTML = content;
        this.nextBtn.className = 'next-btn';
        if (top) {
            this.nextBtn.style.marginTop = `${top}px `;
        }
        if (left) {
            this.nextBtn.style.marginLeft = `${left}px `;
        }
        this.nextBtn.onclick = () => {
            if (state.selection === -1 && this.hasOption) {
                alert('Please select one animation.');
            } else {
                if (state.selection !== -1 && this.hasOption) {
                    recorder.recordAns(state.selection);
                    Reducer.triger(action.UPDATE_READING_SELECTION, -1);
                }
                if (content !== Page.FINISH_BTN_CONTENT) {
                    Tool.pageTrans(this.id);
                } else {
                    recorder.exportResult();
                    Tool.pageTrans(this.id);
                }
            }
        }

        this.container.appendChild(this.nextBtn);
    }

    public createConfirmBtn(idx: number) {
        const confirmBtn: HTMLDivElement = document.createElement('div');
        confirmBtn.innerHTML = 'Confirm';
        confirmBtn.className = 'next-btn';
        this.nextBtn.style.display = 'none';
        confirmBtn.onclick = () => {
            [].forEach.call(document.getElementsByClassName(`readingTrainingCorrect${idx}`), (cOption: HTMLElement) => {
                cOption.style.opacity = '1';
            })
            confirmBtn.style.display = 'none';
            this.nextBtn.style.display = '';
        }
        this.container.appendChild(confirmBtn);
    }

    public createReadingOptions(idx: number, top: number) {
        this.hasOption = true;
        const optionContainer: HTMLDivElement = document.createElement('div');
        optionContainer.className = 'option-container';
        optionContainer.style.marginTop = `${top}px`;
        let readingGifs: string[] = [];
        let correctTraining: number[] = [0, 2, 1];
        switch (idx) {
            case 0:
                this.isTraining = true;
                readingGifs = [
                    'https://tgeconf.github.io/publicUserStudy/videos/reading_train_0_1.mp4',
                    'https://tgeconf.github.io/publicUserStudy/videos/reading_train_0_2.mp4',
                    'https://tgeconf.github.io/publicUserStudy/videos/reading_train_0_3.mp4',
                    'https://tgeconf.github.io/publicUserStudy/videos/reading_train_0_4.mp4'
                ];
                break;
            case 1:
                this.isTraining = true;
                readingGifs = [
                    'https://tgeconf.github.io/publicUserStudy/videos/reading_train_1_1.mp4',
                    'https://tgeconf.github.io/publicUserStudy/videos/reading_train_1_2.mp4',
                    'https://tgeconf.github.io/publicUserStudy/videos/reading_train_1_3.mp4',
                    'https://tgeconf.github.io/publicUserStudy/videos/reading_train_1_4.mp4'
                ];
                break;
            case 2:
                this.isTraining = true;
                readingGifs = [
                    'https://tgeconf.github.io/publicUserStudy/videos/reading_train_2_1.mp4',
                    'https://tgeconf.github.io/publicUserStudy/videos/reading_train_2_2.mp4',
                    'https://tgeconf.github.io/publicUserStudy/videos/reading_train_2_3.mp4',
                    'https://tgeconf.github.io/publicUserStudy/videos/reading_train_2_4.mp4'
                ];
                break;
            case 10:
                readingGifs = [
                    'https://tgeconf.github.io/publicUserStudy/videos/reading_test_1_1.mp4',
                    'https://tgeconf.github.io/publicUserStudy/videos/reading_test_1_2.mp4',
                    'https://tgeconf.github.io/publicUserStudy/videos/reading_test_1_3.mp4',
                    'https://tgeconf.github.io/publicUserStudy/videos/reading_test_1_4.mp4'
                ];
                break;
            case 11:
                readingGifs = [
                    'https://tgeconf.github.io/publicUserStudy/videos/reading_test_2_1.mp4',
                    'https://tgeconf.github.io/publicUserStudy/videos/reading_test_2_2.mp4',
                    'https://tgeconf.github.io/publicUserStudy/videos/reading_test_2_3.mp4',
                    'https://tgeconf.github.io/publicUserStudy/videos/reading_test_2_4.mp4'
                ];
                break;
            case 12:
                readingGifs = [
                    'https://tgeconf.github.io/publicUserStudy/videos/reading_test_3_1.mp4',
                    'https://tgeconf.github.io/publicUserStudy/videos/reading_test_3_2.mp4',
                    'https://tgeconf.github.io/publicUserStudy/videos/reading_test_3_3.mp4',
                    'https://tgeconf.github.io/publicUserStudy/videos/reading_test_3_4.mp4'
                ];
                break;
        }
        for (let i = 0; i < 4; i++) {
            const optionItemContainer: HTMLDivElement = document.createElement('div');
            optionItemContainer.className = 'option-item-container';
            // const optionGif: HTMLImageElement = document.createElement('img');
            // optionGif.src = readingGifs[i];
            const optionVideo: HTMLVideoElement = document.createElement('video');
            optionVideo.controls = true;
            const source: HTMLSourceElement = document.createElement('source');
            source.src = readingGifs[i];
            source.type = 'video/mp4';
            optionVideo.appendChild(source);
            optionItemContainer.appendChild(optionVideo);
            const optionBtn: HTMLInputElement = document.createElement('input');
            optionBtn.type = 'radio';
            optionBtn.className = `readingTraining${idx}`;
            optionBtn.name = `reading${idx}`;
            optionBtn.value = `${i}`;
            optionBtn.onclick = () => {
                // [].forEach.call(document.getElementsByClassName(`readingTraining${idx}`), (option: HTMLElement) => {
                //     (<HTMLInputElement>option).disabled = true;
                // });

                if (optionBtn.checked) {
                    Reducer.triger(action.UPDATE_READING_SELECTION, parseInt(optionBtn.value));
                }
            }
            optionItemContainer.appendChild(optionBtn);

            if (this.isTraining) {
                const optionBtn2: HTMLInputElement = document.createElement('input');
                optionBtn2.className = `readingTrainingCorrect${idx} correct`;
                optionBtn2.type = 'radio';
                optionBtn2.disabled = true;
                optionBtn2.name = `readingTraining${idx}`;
                optionBtn2.value = `${i}`;
                optionBtn2.style.opacity = '0';
                if (i === correctTraining[idx]) {
                    optionBtn2.checked = true;
                }
                optionItemContainer.appendChild(optionBtn2);

            }

            optionContainer.appendChild(optionItemContainer);
        }
        this.container.appendChild(optionContainer);
    }


    public createTable(questions: string[], ueIdx: number) {
        const table: HTMLTableElement = document.createElement('table');
        const tr1: HTMLTableRowElement = document.createElement('tr');
        const td1: HTMLTableDataCellElement = document.createElement('td');
        td1.innerHTML = 'Questions';
        tr1.appendChild(td1);
        const likertScales: string[] = ['strongly agree', 'agree', 'neutral', 'disagree', 'strongly disagree'];
        likertScales.forEach((s: string) => {
            const tmpTd: HTMLTableDataCellElement = document.createElement('td');
            tmpTd.innerHTML = s;
            tr1.appendChild(tmpTd);
        })
        table.appendChild(tr1);
        questions.forEach((q: string, idx: number) => {
            const tr: HTMLTableRowElement = document.createElement('tr');
            const tmpTd1: HTMLTableDataCellElement = document.createElement('td');
            tmpTd1.innerHTML = q;
            tr.appendChild(tmpTd1);
            for (let i = 0, len = likertScales.length; i < len; i++) {
                const optionBtn: HTMLInputElement = document.createElement('input');
                optionBtn.className = `readingTrainingCorrect${idx}`;
                optionBtn.type = 'radio';
                optionBtn.name = `ue${ueIdx}_${idx}`;
                optionBtn.value = `${5 - i}`;
                optionBtn.onclick = () => {
                    if (optionBtn.checked) {
                        recorder.recordUe(`ue${ueIdx}_${idx}`, 5 - i);
                    }
                }
                const tmpTd: HTMLTableDataCellElement = document.createElement('td');
                tmpTd.appendChild(optionBtn);
                tr.appendChild(tmpTd);
            }
            table.appendChild(tr);
        })
        this.container.appendChild(table);
    }
}