export interface IUserInfo {
    name: string
    researchArea: string
    vToolExp: string
}

class Recorder {
    public userInfo: IUserInfo = { name: 'null', researchArea: '', vToolExp: '' };
    public answers: number[] = [];
    public ue: Map<string, number> = new Map();

    public recordUserInfo(userInfo: IUserInfo) {
        this.userInfo = userInfo;
    }

    public recordAns(ans: number) {
        this.answers.push(ans);
    }

    public recordUe(q: string, score: number) {
        this.ue.set(q, score);
    }

    public exportResult() {
        const result = {
            userInfo: this.userInfo,
            answers: this.answers,
            ue: [...this.ue]
        }
        const file = new Blob([JSON.stringify(result, null, 2)], { type: 'application/json' });
        const fileName = `${this.userInfo.name}.json`;
        if (window.navigator.msSaveOrOpenBlob) // IE10+
            window.navigator.msSaveOrOpenBlob(file, fileName);
        else { // Others
            var a = document.createElement("a"),
                url = URL.createObjectURL(file);
            a.href = url;
            a.download = fileName;
            document.body.appendChild(a);
            a.click();
            setTimeout(function () {
                document.body.removeChild(a);
                window.URL.revokeObjectURL(url);
            }, 0);
        }
    }

}

export let recorder = new Recorder();